export default {
	data() {
		return {
			id:0,
			datalist:[],
			page:1,
			pagesize:10,
			click_is:true
		}
	},
	onLoad(options) {
		this.id = options.id
		this.getdata()
	},
	methods: {
		getdata(){
			let  that = this
			uni.request({
				url:that.common.websiteUrl+"experhome_owners_commentslist",
				header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
				method:"post",
				data:{
						'exper_id':that.id,
						'page':that.page,
						'pagesize':that.pagesize
						// 'id':that.id
					},
				success: (res) => {
					if(res.data.code==200){
						that.datalist =res.data.data
					}else{
						that.click_is = false
					}
					console.log(res.data.data)
				},fail() {
					that.click_is = false
					that.common.network()
				}
			});
			console.log('评论')
		},
		onReachBottom() {
			let _self = this
			if(_self.click_is==true){
				_self.page = _self.page+1
				_self.getdata()
			}
			
		},
		back(){
			uni.navigateTo({
			    url: '../experiencedetail/experiencedetail'
			});
		},
		getimgurl(image){
			return this.common.websiteUrl+image
		},
	}
}
